                <div class="highlight" slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">'inline'</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">bee-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"base(0)"</span>&gt;</span>默认<span class="hljs-tag">&lt;/<span class="hljs-name">bee-button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">bee-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"base(1)"</span>&gt;</span>自定义标题<span class="hljs-tag">&lt;/<span class="hljs-name">bee-button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">bee-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"base(2)"</span>&gt;</span>取消回调<span class="hljs-tag">&lt;/<span class="hljs-name">bee-button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">bee-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"base(3)"</span>&gt;</span>确定回调<span class="hljs-tag">&lt;/<span class="hljs-name">bee-button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">bee-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"base(4)"</span>&gt;</span>关闭回调<span class="hljs-tag">&lt;/<span class="hljs-name">bee-button</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  <span class="hljs-attr">methods</span>: {
    base (type) {
      <span class="hljs-keyword">let</span> options = {
        <span class="hljs-attr">message</span>: <span class="hljs-string">'这是一段通知内容'</span>
      }

      <span class="hljs-keyword">if</span> (type &gt;= <span class="hljs-number">1</span>) {
        options.title = <span class="hljs-string">'自定义标题'</span>
      }
      
      <span class="hljs-keyword">if</span> (type &gt;= <span class="hljs-number">2</span>) {
        options.cancelBtnFun = <span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
          <span class="hljs-keyword">this</span>.$_createMessage({
            <span class="hljs-attr">message</span>: <span class="hljs-string">'已取消'</span>
          }).show()
        }
      }
      
      <span class="hljs-keyword">if</span> (type &gt;= <span class="hljs-number">3</span>) {
        options.confirmBtnFun = <span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
          <span class="hljs-keyword">this</span>.$_createMessage({
            <span class="hljs-attr">message</span>: <span class="hljs-string">'已确定'</span>,
            <span class="hljs-attr">type</span>: <span class="hljs-string">'success'</span>
          }).show()
        }
      }
      
      <span class="hljs-keyword">if</span> (type &gt;= <span class="hljs-number">4</span>) {
        options.closeBtnFun = <span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
          <span class="hljs-keyword">this</span>.$_createMessage({
            <span class="hljs-attr">message</span>: <span class="hljs-string">'已关闭'</span>,
            <span class="hljs-attr">type</span>: <span class="hljs-string">'error'</span>
          }).show()
        }
      }

      <span class="hljs-keyword">this</span>.$_createAlert(options).show()
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
