    center (type) {
      <span class="hljs-keyword">let</span> options = {
        <span class="hljs-attr">title</span>: <span class="hljs-string">'居中方式'</span>,
        <span class="hljs-attr">message</span>: <span class="hljs-string">'这是一段提示信息'</span>,
        <span class="hljs-attr">align</span>: type
      }

      <span class="hljs-keyword">this</span>.$_createAlert(options).show()
