<h3>options</h3>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>title</td>
<td>标题</td>
<td>string</td>
<td>-</td>
<td>提示</td>
</tr>
<tr>
<td>message</td>
<td>提示的文字信息</td>
<td>string</td>
<td>—</td>
<td>-</td>
</tr>
<tr>
<td>html</td>
<td>提示的HTML代码</td>
<td>string</td>
<td>—</td>
<td>-</td>
</tr>
<tr>
<td>align</td>
<td>内容的对齐方式</td>
<td>string</td>
<td>horizontal,vertial,center</td>
<td>-</td>
</tr>
<tr>
<td>cancelBtnVisiable</td>
<td>是否显示取消按钮</td>
<td>boolean</td>
<td>-</td>
<td>true</td>
</tr>
<tr>
<td>cancelBtnText</td>
<td>取消按钮显示的文字</td>
<td>string</td>
<td>-</td>
<td>取消</td>
</tr>
<tr>
<td>cancelBtnFun</td>
<td>取消按钮绑定的事件函数</td>
<td>function</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>confirmBtnVisiable</td>
<td>是否显示确定按钮</td>
<td>boolean</td>
<td>-</td>
<td>true</td>
</tr>
<tr>
<td>confirmBtnText</td>
<td>确定按钮显示的文字</td>
<td>string</td>
<td>-</td>
<td>确定</td>
</tr>
<tr>
<td>confirmBtnFun</td>
<td>确定按钮绑定的事件函数</td>
<td>function</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>closeBtnVisiable</td>
<td>是否显示关闭按钮</td>
<td>boolean</td>
<td>-</td>
<td>true</td>
</tr>
<tr>
<td>closeBtnFun</td>
