</section></template>
<style>
.inline .btn--wp {
  margin: 0 10px 10px 0;
}
</style>
<script>
export default {
  methods: {
    base (type) {
      let options = {
        message: '这是一段通知内容'
      }

      if (type >= 1) {
        options.title = '自定义标题'
      }
      
      if (type >= 2) {
        options.cancelBtnFun = () => {
          this.$_createMessage({
            message: '已取消'
          }).show()
        }
      }
      
      if (type >= 3) {
        options.confirmBtnFun = () => {
          this.$_createMessage({
            message: '已确定',
            type: 'success'
          }).show()
        }
      }
      
      if (type >= 4) {
        options.closeBtnFun = () => {
          this.$_createMessage({
            message: '已关闭',
            type: 'error'
          }).show()
        }
      }

      this.$_createAlert(options).show()
    },

    center (type) {
      let options = {
        title: '居中方式',
        message: '这是一段提示信息',
        align: type
      }

      this.$_createAlert(options).show()
    },

    useHtml () {
      this.$_createAlert({
        title: 'use html',
        html: `这是一段<span style='color: red; font-size: 18px;'>HTML</span>内容`
      }).show()
    },

    noClose () {
      this.$_createAlert({
        title: 'no close',
        closeBtnVisiable: false
      }).show()
    }
  }
}
</script>